<template>
	<view class="body">
		<image class="banner" src="../../static/volunteer/look_banner.png" mode="widthFix">
			<view class="num">目前共{{data.total}}人拟报名该院校</view>
		</image>
		<view class="list">
			<view class="cell" v-for="(item,index) in data.data" :key="index">
				<view class="cellLeft">
					<view class="nums">{{index+1}}</view>
					<image :src="item.avatar ||'../../static/mine/user_head.png'"></image>
					<view class="name">{{item.nickname || '匿名用户'}}</view>
				</view>
				<view class="cellRight">一站成硕</view>
			</view>
		</view>
	</view>
</template>

<script>
	import store from '@/store/index.js'; //引入store
	import { check } from '@/common/http.api.js';
	export default{
		data() {
			return {
				token:'',
				data:{}
			}
		},
		onLoad() {
			this.token = store.state.vuex_token
			check({token:this.token}).then(res=>{
				console.log(res)
				this.data = res.data.data
			}).catch(err=>{
				console.log(err)
			})
		},
	}
</script>

<style lang="scss">
	.body{
		width: 100vw;
		padding: 2vh 0;
		min-height: 96vh;
		background-color: #f7f6fb;
		position: relative;
		.banner{
			width: 100vw;
		}
		.num{
			position: absolute;
			top: 270rpx;
			left: 50%;
			right: 50%;
			line-height: 68rpx;
			background-color: #fff;
			color: #2e72ca;
			width: 420rpx;
			font-size: 30rpx;
			text-align: center;
			border-radius: 50rpx;
			transform: translate(-50%, -50%);
		}
		.list{
			position: absolute;
			top: 360rpx;
			left: 5%;
			right: 5%;
			padding: 30rpx;
			background-color: #fff;
			border-radius: 20rpx;
			.cell{
				display: flex;
				justify-content: space-between;
				align-items: center;
				margin-bottom: 40rpx;
				.cellLeft{
					display: flex;
					align-items: center;
					font-size: 28rpx;
					.nums{
						margin-right: 70rpx;
					}
					image{
						width: 70rpx;
						height: 70rpx;
						border-radius: 50%;
						margin-right: 40rpx;
					}
				}
				.cellRight{
					font-size: 30rpx;
					color: #ee0000;
				}
			}
			.cell:last-child{
				margin-bottom: 0;
			}
		}
	}
</style>